import React from "react";
import { Typography, Grid, Stack } from "@mui/joy";
import { Bar } from "react-chartjs-2";
import { GET_ALL_VALIDATION_T } from "../../types";
import TableCustom from "../../components/TableCustome";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { faArrowDown, faArrowUp } from "@fortawesome/free-solid-svg-icons";
import {
    Chart as ChartJS,
    CategoryScale,
    LinearScale,
    BarElement,
    Title,
    Tooltip,
    Legend,
} from "chart.js";
import { green, grey, orange, red } from "@mui/material/colors";
import formatDateToDDMMYYYY from "../../utils/formatDateToDDMMYYYY";
import { getPrixProduitMarcheParRegion } from "../../utils/getPrixProduitMarcheParRegion";

ChartJS.register(
    CategoryScale,
    LinearScale,
    BarElement,
    Title,
    Tooltip,
    Legend
);

// Dfinir la nouvelle interface pour les donnes entrantes du composant
interface MarcheCollectes {
    marche: string;
    collectes: GET_ALL_VALIDATION_T[];
}

const PageContentProduitRegion: React.FC<{
    produit: string;
    region: string;
    data: MarcheCollectes[]; // La donne reue est un tableau de MarcheCollectes
}> = ({ produit, region, data }) => {
    // Utiliser la fonction utilitaire pour traiter les donnes et obtenir les informations ncessaires
    const processedData = getPrixProduitMarcheParRegion(data);

    // Trier les marchs pour l'affichage
    const marches = processedData.map((p) => p.marche);

    // Prparer les lignes du tableau avec les donnes traites, en incluant les "gaps"
    const tableRows = [
        {
            label: "Prix actuel",
            values: processedData.map((p) =>
                p.prixActuel !== null ? Math.round(p.prixActuel).toLocaleString() + " FCFA" : "-"
            ),
            center: true
        },
        {
            label: "Date actuelle",
            values: processedData.map((p) => formatDateToDDMMYYYY(p.dateActuelle) || "-"),
        },
        {
            label: "Nbr de collectes actuel",
            values: processedData.map((p) => (p.nbrCollecteActuel !== null ? p.nbrCollecteActuel.toString() : "-")),
        },
        'gap', // Ajout d'une ligne de sparation aprs les donnes actuelles
        {
            label: "Prix prcdent",
            values: processedData.map((p) =>
                p.prixPrecedent !== null ? Math.round(p.prixPrecedent).toLocaleString() + " FCFA" : "-"
            ),
        },
        {
            label: "Date prcdente",
            values: processedData.map((p) => formatDateToDDMMYYYY(p.datePrecedente) || "-"),
        },
        {
            label: "Nbr de collectes prcdent",
            values: processedData.map((p) => (p.nbrCollectePrecedente !== null ? p.nbrCollectePrecedente.toString() : "-")),
        },
        'gap', // Ajout d'une ligne de sparation aprs les donnes prcdentes
        {
            label: "Evolution %",
            values: processedData.map((p) => {
                if (p.evolution === null) return "-";
                const arrow = p.evolution > 0 ? "" : p.evolution < 0 ? "" : "";
                return `${arrow} ${Math.abs(p.evolution).toFixed(2)}%`;
            }),
        },
    ];

    return (
        <Stack sx={{ gap: 3, p: 3, }} height={'100%'}>
            <Typography
                level="h4"
                fontSize={"2vw"}
                textColor={grey[700]}
                fontWeight={300}
            >
                Prix de
                {` `}<Typography
                    fontWeight={700}
                    textColor={green[900]}
                >{produit}</Typography> {` `}
                dans la rgion de
                {` `}
                <Typography
                    fontWeight={700}
                    textColor={red[900]}
                >{region}</Typography>
            </Typography>

            <Grid container spacing={5} height={'100%'} flex={1} alignItems="center">
                <Grid xs={12}>
                    <TableCustom
                        columns={[
                            { label: "#", key: "label" },
                            ...marches.map((m) => ({ label: m, key: m, center : true })),
                        ]}
                        data={tableRows.map((row) => {
                            if (row === 'gap') {
                                return 'gap'; // Retourne la chane de caractres si c'est un "gap"
                            }

                            //@ts-ignore
                            const obj: any = { label: row.label };
                            //@ts-ignore
                            row.values.forEach((v, i) => {
                                obj[marches[i]] = v;
                            });
                            return obj;
                        })}
                    />
                </Grid>
            </Grid>
        </Stack>
    );
};


export default PageContentProduitRegion;